import React from "react";
import Headers from "../component/Headers";
import { BASE_URL, API } from "../../Endpoint";
import {
  Row,
  Col,
  Table,
  Button,
  notification,
  Spin,
  Card,
  Input,
  message,
} from "antd";
import { withRouter } from "react-router-dom";
import { token } from "../../Helper/Session";

const { Search } = Input;
class ShippingRequestList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      shipping_request: [],
      isLoadingRequestList: true,
      isSearching: false,
      track_data: [],
    };
  }

  gotToAddSR = () => {
    this.props.history.push("/add-shipping-request");
  };

  componentDidMount() {
    const queryParams = new URLSearchParams(window.location.search);
    const status = queryParams.get("status");
    const from_date = queryParams.get("from_date");
    const to_date = queryParams.get("to_date");

    this.setState({ isLoadingRequestList: true });
    fetch(`${BASE_URL}${API.SHIPPING_REQUEST_LIST}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        status: status ? status : "",
        from_date: from_date ? from_date : "",
        to_date: to_date ? to_date : "",
      }),
    })
      .then((res) => res.json())
      .then((json) => {
        this.setState({ isLoadingRequestList: false });
        if (json.status == true) {
          this.setState({
            shipping_request: json.data,
          });
          notification.success({
            message: json.success_msg,
          });
        } else {
          notification.error({
            message: json.error_msg,
          });
        }
      });
  }

  handleTracking = (value) => {
    this.setState({ isSearching: true });
    fetch(`${BASE_URL}${API.SHIPMENT_TRACKING}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        tracking_id: value,
      }),
    })
      .then((res) => res.json())
      .then((json) => {
        this.setState({ isSearching: false });
        if (json.status == true) {
          this.setState({
            track_data: json.data,
          });
          this.props.history.push("/shipping-request/view/" + json.data.id);
        } else {
          message.error({
            content: json.error_msg,
            duration: 2,
          });
        }
      });
  };

  render() {
    const { shipping_request, isLoadingRequestList, isSearching } = this.state;

    var data = [];
    const columns = [
      {
        title: "SNo",
        dataIndex: "key",
      },
      {
        title: "Request Id",
        dataIndex: "request_id",
      },
      {
        title: "Service Now Id",
        dataIndex: "servicenow_id",
      },
      {
        title: "Cutomer Name",
        dataIndex: "customer_name",
      },
      {
        title: "To Comapny",
        dataIndex: "to_company",
      },
      {
        title: "To Contact Name",
        dataIndex: "to_contact",
      },
      {
        title: "From Comapny",
        dataIndex: "from_company",
      },
      {
        title: "From Contact Name",
        dataIndex: "from_contact",
      },
      {
        title: "Action",
        dataIndex: "action",
      },
    ];
    shipping_request.map((sr, i) =>
      data.push({
        key: i + 1,
        request_id: sr["request_id"],
        servicenow_id: sr["servicenow_id"],
        to_contact: sr["to_contact_name"],
        to_company: sr["to_company_name"],
        customer_name: sr["customer_name"],
        from_company: sr["from_company_name"],
        from_contact: sr["from_contact_name"],
        action: (
          <Button
            type="secondary"
            size="small"
            onClick={() => {
              this.props.history.push("/shipping-request/view/" + sr["id"]);
            }}
          >
            View
          </Button>
        ),
      })
    );

    return (
      <Headers
        bodyContain={
          <>
            <Row>
              <Col md={24} sm={24} xs={24}>
                <Card className="card">
                  <Row>
                    <Col md={3}>
                      <h3>Shipping Request</h3>
                    </Col>
                    <Col md={15} className="p5">
                      <Search
                        type="secondary"
                        placeholder="Enter shipment tracking Id"
                        enterButton="Search"
                        size="medium"
                        loading={isSearching}
                        onSearch={this.handleTracking}
                      />
                    </Col>
                    <Col>
                      <Button
                        onClick={this.gotToAddSR} ///{this.gotToAddSR}
                        type="warning"
                        style={{ marginBottom: "10px" }}
                        shape="round"
                        // size="large"
                      >
                        Add Shipping Request
                      </Button>
                    </Col>
                  </Row>
                </Card>
              </Col>
            </Row>
            <div className="site-layout-background">
              <Spin spinning={isLoadingRequestList}>
                <Row>
                  <Col md={24}>
                    <Table
                      columns={columns}
                      dataSource={data}
                      size="middle"
                      bordered
                      loading="false"
                    />
                  </Col>
                </Row>
              </Spin>
            </div>
          </>
        }
      />
    );
  }
}
export default withRouter(ShippingRequestList);
