import React from "react";
import Headers from "../component/Headers";
import { BASE_URL, API } from "../../Endpoint";
import { message, Upload } from "antd";
import {
  UploadOutlined,
  MinusCircleTwoTone,
  PlusCircleTwoTone,
} from "@ant-design/icons";
import moment from "moment";

import {
  Row,
  Col,
  Divider,
  notification,
  Table,
  Spin,
  Tag,
  Modal,
  Button,
  Input,
  DatePicker,
  Dropdown,
  Space,
  Menu,
  Badge,
  Card,
  Steps,
} from "antd";

import Form from "../../Helper/Form";

import { token, sessionData } from "../../Helper/Session";

// console.log(sessionData);

const { Step } = Steps;
class ViewShippingRequest extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      shipping_request_id: this.props.id,
      isToAddrLoad: false,
      isFromAddrLoad: false,
      isBtnLoad: false,
      isLoadingShippingRequest: false,
      request: [],
      visibleModal: false,
      items: [],
      isShipmentDelivered: false,
      shipping_id: "",
      status_id: 0, //shipping_status_id
      item_id: 0,
      shipping_status_data: [],
      file: "",
      file_url: "",
    };
  }

  formRefDel = React.createRef();
  formRefLab = React.createRef();
  componentDidMount() {
    this.setState({ isLoadingShippingRequest: true });
    fetch(`${BASE_URL}${API.SHIPPING_REQUEST_DETAIL}`, {
      method: "post",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        shipping_request_id: this.state.shipping_request_id,
      }),
    })
      .then((res) => res.json())
      .then((json) => {
        this.setState({ isLoadingShippingRequest: false });
        if (json.status === true) {
          this.setState({
            request: json.data,
            shipping_id: json.data.id,
            items: json.data.items,
          });
          notification.success({ message: json.success_msg, placement: "top" });
        } else {
          notification.error({ message: json.error_msg, placement: "top" });
        }
      });
  }

  handleUpdateStatus = (item_id, status_id) => {
    // alert(item_id + " " + status_id);
    if (status_id == 1) {
      this.setState({
        status_id: status_id,
        item_id: item_id,
        visibleModal: true,
      });
      this.get_items_status(status_id, item_id);
    } else if (status_id == 3) {
      this.setState({
        item_id: item_id,
        status_id: status_id,
        isShipmentDelivered: true,
      });
      this.get_items_status(status_id, item_id);
    } else if (status_id == 2) {
      this.setState({
        item_id: item_id,
        status_id: status_id,
      });
      fetch(`${BASE_URL}${API.UPDATE_SHIPPING_STATUS}`, {
        method: "POST",
        headers: {
          Accept: "application/form-data",
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({
          shipping_id: this.state.shipping_id,
          status: status_id,
          item_id: item_id,
        }),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json.status == true) {
            message.success(json.success_msg);
          } else {
            message.error(json.error_msg);
          }
        });
    }
  };

  saveLabelCreation = (value) => {
    fetch(`${BASE_URL}${API.UPDATE_SHIPPING_STATUS}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        shipping_id: this.state.shipping_id,
        label_creation_date: value.creation_date,
        carrier: value.carrier,
        tracking: value.tracking_no,
        pickup_date: value.pickup_date,
        pickup_time: value.pickup_time,
        url: value.tracking_url,
        status: this.state.status_id,
        label: value.shipping_label["file"]["name"],
        item_id: this.state.item_id,
      }),
    })
      .then((res) => res.json())
      .then((json) => {
        if (json.status === true) {
          this.setState({ visibleModal: false });
          notification.success({ message: json.success_msg, placement: "top" });
        } else {
          notification.error({ message: json.error_msg, placement: "top" });
        }
      });
  };

  saveDelivery = (value) => {
    fetch(`${BASE_URL}${API.UPDATE_SHIPPING_STATUS}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        shipping_id: this.state.shipping_id,
        delivery_date: value.delivery_date,
        status: this.state.status_id,
        pod: value.pod["file"]["name"],
        item_id: this.state.item_id,
      }),
    })
      .then((res) => res.json())
      .then((json) => {
        if (json.status === true) {
          this.setState({ isShipmentDelivered: false });
          notification.success({ message: json.success_msg, placement: "top" });
        } else {
          notification.error({ message: json.error_msg, placement: "top" });
        }
      });
  };

  get_items_status = (status, line_item_id) => {
    this.setState({
      file: "",
      file_url: "",
    });
    fetch(`${BASE_URL}${API.SHIPPING_STATUS_DETAIL}`, {
      method: "post",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        shipping_id: this.state.shipping_request_id,
        line_item_id: line_item_id,
        status: status,
      }),
    })
      .then((res) => res.json())
      .then((json) => {
        if (json.status === true) {
          this.setState({
            shipping_status_data: json.data,
          });
          if (status == 1) {
            this.formRefLab.current.setFieldsValue({
              carrier: this.state.shipping_status_data.carrier,
              creation_date: moment(
                this.state.shipping_status_data.label_creation_date
              ),
              tracking_no: this.state.shipping_status_data.tracking,
              tracking_url: this.state.shipping_status_data.url,
              pickup_date: moment(this.state.shipping_status_data.pickup_date),
              pickup_time: this.state.shipping_status_data.pickup_time,
            });
            this.setState({
              file: json.data.label,
              file_url: json.data.label_url,
            });
          } else if (status == 3) {
            this.formRefDel.current.setFieldsValue({
              delivery_date: moment(
                this.state.shipping_status_data.delivery_date
              ),
            });
            this.setState({
              file: json.data.pod,
              file_url: json.data.pod_url,
            });
          }
        }
      });
  };

  render() {
    const {
      request,
      items,
      isLoadingShippingRequest,
      visibleModal,
      isShipmentDelivered,
      shipping_id,
    } = this.state;
    const columns = [
      {
        title: "SNo",
        dataIndex: "key",
      },
      {
        title: "Item Name",
        dataIndex: "name",
      },
      {
        title: "Quantity",
        dataIndex: "qty",
      },
      {
        title: "Weight(kg)",
        dataIndex: "weight",
      },
      {
        title: "Length(mm)",
        dataIndex: "length",
      },
      {
        title: "Width(mm)",
        dataIndex: "width",
      },
      {
        title: "Height(mm)",
        dataIndex: "height",
      },
      {
        title: "Action",
        dataIndex: "action",
      },
    ];

    if (sessionData.role_id == 3) {
      columns.pop();
    }

    var data = [];
    items.map((itm, i) => {
      data.push({
        key: i + 1,
        name: itm.name,
        qty: itm.qty,
        weight: itm.weight,
        length: itm.length,
        width: itm.width,
        height: itm.height,
        value: itm.value,
        content: itm.content,
        description: itm.description,
        action: (
          <>
            <Space wrap>
              <Dropdown.Button
                overlay={
                  <Menu
                    items={[
                      {
                        key: "1",
                        label: (
                          <Button
                            type="primary"
                            className="actionButton"
                            onClick={() => this.handleUpdateStatus(itm.id, 1)}
                          >
                            Shipping label created
                          </Button>
                        ),
                      },
                      {
                        key: "2",
                        label: (
                          <Button
                            type="secondary"
                            className="actionButton"
                            onClick={() => this.handleUpdateStatus(itm.id, 2)}
                          >
                            Shipment Intransit
                          </Button>
                        ),
                      },
                      {
                        key: "3",
                        label: (
                          <Button
                            type="warning"
                            className="actionButton"
                            onClick={() => this.handleUpdateStatus(itm.id, 3)}
                          >
                            Shipment delivered
                          </Button>
                        ),
                      },
                    ]}
                  />
                }
                type="dashed"
                placement="left"
              >
                Action
              </Dropdown.Button>
            </Space>
            {/* <br/>
            <Badge.Ribbon text={itm.status_name} color={"green"}/> */}
          </>
        ),
      });
    });
    const fileList = [
      {
        uid: "1",
        name: this.state.file,
        status: this.state.file ? "done" : "error",
        url: this.state.file_url,
      },
    ];
    // console.log(fileList);
    const props = {
      action: `${BASE_URL}${API.UPLOAD_DOCS}`,
      multiple: false,
      maxCount: 1,
      headers: {
        Accept: "application/form-data",
        Authorization: "Bearer " + token,
      },
      fileList: fileList[0].status == "done" ? fileList : [],
      onChange(info) {
        // if (info.file.status !== "uploading") {
        //   console.log(info.file, info.fileList);
        // }
        if (info.file.status === "done") {
          message.success(`${info.file.name} file uploaded successfully`);
        } else if (info.file.status === "error") {
          message.error(`${info.file.name} file upload failed.`);
        }
      },
      onRemove(v) {
        console.log(v);
      },
    };
    return (
      <Headers
        bodyContain={
          <>
            <div className="site-layout-background form-design">
              <h4>Shipping Request Id : {request.request_id} </h4>
              <Steps current={parseInt(request.shipping_status)}>
                <Step
                  title="Request Placed"
                  description="This mean shipping for this request is placed."
                />
                <Step
                  title="Shipping label created"
                  description="This mean user label uploaded for this shipping request."
                />
                <Step
                  title="Intransit"
                  description="Your shipping request is Intransit."
                />
                <Step title="Delivered" description="Shipiment is delivered." />
              </Steps>
              <br />
              {request.shipping_status == 0 && (
                <Tag color="#002140">Request placed</Tag>
              )}
              {request.shipping_status == 1 && (
                <Tag color="#019b04">Shipping Label created</Tag>
              )}
              {request.shipping_status == 2 && (
                <Tag color="#e2ca10">Intransit</Tag>
              )}
              {request.shipping_status == 3 && (
                <Tag color="#1b74c6">Delivered</Tag>
              )}
              <Divider />
              <div className="shipping_detail">
                <Spin spinning={isLoadingShippingRequest}>
                  <Row>
                    {/* <Col md={12}>
                      <h5>Shipping Request Id</h5>
                      <p>{request.request_id}</p>
                    </Col> */}
                    <Col md={12}>
                      <p style={{fontWeight:"600"}}>Servicenow Id</p>
                      <p>{request.servicenow_id}</p>
                    </Col>
                    <Col md={12}>
                      <p style={{fontWeight:"600"}}>Customer User</p>
                      <p>{request.first_name + " " + request.last_name}</p>
                    </Col>
                  </Row>
                  <Row>
                    {/* <Col md={12}>
                      <Card className="address">
                        <p className="text-center">Shipping to Address</p>
                        <Row>
                          <Col sm={12}>
                            <p>Company Name</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.to_company_name}</p>
                          </Col>
                          <Col sm={12}>
                            <p>Contact Name</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.to_contact_name}</p>
                          </Col>

                          <Col sm={12}>
                            <p>Email</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.to_email}</p>
                          </Col>
                          <Col sm={12}>
                            <p>Street</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.to_street}</p>
                          </Col>
                          <Col sm={12}>
                            <p>City</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.to_city}</p>
                          </Col>
                          <Col sm={12}>
                            <p>State</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.to_state}</p>
                          </Col>
                          <Col sm={12}>
                            <p>Country</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.to_country}</p>
                          </Col>
                        </Row>
                      </Card>
                    </Col> */}
                    <Col md={12}>
                      <Badge.Ribbon text="Shipping to Address">
                        <Card className="address">
                          <p className="text-center"></p>
                          <Row>
                            <Col sm={24}>
                              <p>
                                {request.to_contact_name +
                                  ", " +
                                  request.to_company_name}
                              </p>
                            </Col>
                            <Col sm={24}>
                              <p>
                                {request.to_street +
                                  ", " +
                                  request.to_city +
                                  ", " +
                                  request.to_state +
                                  ", " +
                                  request.to_country +
                                  " " +
                                  (request.to_postal
                                    ? " - " + request.to_postal
                                    : "")}
                              </p>
                            </Col>
                            <Col sm={24}>
                              <p>
                                {(request.to_contact_number
                                  ? request.to_contact_number + "/ "
                                  : "") +
                                  "" +
                                  request.to_email}
                              </p>
                            </Col>
                          </Row>
                        </Card>
                      </Badge.Ribbon>
                    </Col>
                    {/* <Col md={12}>
                      <Card md={6} className="address">
                        <h4 className="text-center">Shipping from Address</h4>
                        <Row>
                          <Col sm={12}>
                            <p>Company Name</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.from_company_name}</p>
                          </Col>
                          <Col sm={12}>
                            <p>Contact Name</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.from_contact_name}</p>
                          </Col>
                          <Col sm={12}>
                            <p>Email</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.from_email}</p>
                          </Col>
                          <Col sm={12}>
                            <p>Street</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.from_street}</p>
                          </Col>
                          <Col sm={12}>
                            <p>City</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.from_city}</p>
                          </Col>
                          <Col sm={12}>
                            <p>State</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.from_state}</p>
                          </Col>
                          <Col sm={12}>
                            <p>Country</p>
                          </Col>
                          <Col sm={12}>
                            <p>{request.from_country}</p>
                          </Col>
                        </Row>
                      </Card>
                    </Col> */}
                    <Col md={12}>
                      <Badge.Ribbon
                        text="Shipping from Address"
                        color={"green"}
                      >
                        <Card md={6} className="address">
                          <p className="text-center"></p>
                          <Row>
                            <Col sm={24}>
                              <p>
                                {request.from_contact_name +
                                  ", " +
                                  request.from_company_name}
                              </p>
                            </Col>

                            <Col sm={24}>
                              <p>
                                {request.from_street +
                                  ", " +
                                  request.from_city +
                                  ", " +
                                  request.from_state +
                                  ", " +
                                  request.from_country +
                                  " " +
                                  (request.from_postal
                                    ? " - " + request.from_postal
                                    : "")}
                              </p>
                            </Col>
                            <Col sm={24}>
                              <p>
                                {(request.from_contact_number
                                  ? request.from_contact_number + "/ "
                                  : "") +
                                  "" +
                                  request.from_email}
                              </p>
                            </Col>
                          </Row>
                        </Card>
                      </Badge.Ribbon>
                    </Col>
                  </Row>

                  <div className="line_item"></div>
                  <Table
                    expandable={{
                      expandedRowRender: (record) => (
                        <>
                          <p style={{ margin: 1 }}>
                            Content :{" "}
                            <span className="rowExp">{record.content}</span>
                          </p>
                          <p style={{ margin: 1 }}>
                            value :{" "}
                            <span className="rowExp">{record.value}</span>
                          </p>
                          <p style={{ margin: 0 }}>{record.description}</p>
                        </>
                      ),
                      expandIcon: ({ expanded, onExpand, record }) =>
                        expanded ? (
                          <MinusCircleTwoTone
                            onClick={(e) => onExpand(record, e)}
                          />
                        ) : (
                          <PlusCircleTwoTone
                            onClick={(e) => onExpand(record, e)}
                          />
                        ),
                    }}
                    dataSource={data}
                    columns={columns}
                    pagination={false}
                  />
                </Spin>
              </div>
            </div>
            {/* Modal start */}
            <Modal
              style={{ top: 20 }}
              visible={visibleModal}
              title="Update Shipping label created"
              onCancel={() => {
                this.setState({ visibleModal: false });
              }}
              destroyOnClose={true}
              footer={[
                <Button
                  type=""
                  onClick={() => {
                    this.setState({ visibleModal: false });
                  }}
                >
                  Cancel
                </Button>,
              ]}
              width={800}
            >
              <div className="form-design">
                <Form
                  layout="vertical"
                  onFinish={this.saveLabelCreation}
                  scrollToFirstError
                  ref={this.formRefLab}
                >
                  <Row>
                    <Col md={12}>
                      <Form.Item
                        name="creation_date"
                        label="Label creation date"
                        rules={[
                          {
                            required: true,
                            message: "Please enter label creation date!",
                          },
                        ]}
                      >
                        <DatePicker
                          style={{ width: "100%" }}
                          format="DD-MM-YYYY"
                        />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="carrier"
                        label="Carrier"
                        rules={[
                          {
                            required: true,
                            message: "Please enter carrier!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="tracking_no"
                        label="Tracking"
                        rules={[
                          {
                            required: true,
                            message: "Please enter tracking!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="tracking_url"
                        label="Tracking URL"
                        rules={[
                          {
                            required: true,
                            message: "Please enter tracking url!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="pickup_date"
                        label="Pick up date"
                        rules={[
                          {
                            required: true,
                            message: "Please enter pickup date!",
                          },
                        ]}
                      >
                        <DatePicker
                          style={{ width: "100%" }}
                          format="DD-MM-YYYY"
                        />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="pickup_time"
                        label="Pickup time"
                        rules={[
                          {
                            required: true,
                            message: "Please enter pickup time!",
                          },
                        ]}
                      >
                        {/* <TimePicker
                          showTime={{ use12Hours: true, format: "HH:mm A" }}
                          format={"HH:mm:a"}
                        /> */}
                        <input
                          type={"time"}
                          style={{
                            border: "1px solid #d9d9d9",
                            padding: "3px 11px 4px",
                            borderRadius: "2px",
                          }}
                        />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="shipping_label"
                        label="shipping label"
                        rules={[
                          {
                            required: true,
                            message: "Please select shipping label!",
                          },
                        ]}
                      >
                        <Upload
                          {...props}
                          data={{ shipping_id: shipping_id, doc_type: "label" }}
                        >
                          <Button icon={<UploadOutlined />}>
                            Click to Upload
                          </Button>
                        </Upload>
                      </Form.Item>
                    </Col>
                  </Row>
                  <Row>
                    <Col md={12}>
                      <Form.Item>
                        <Button type="primary" htmlType="submit" size="middle">
                          Save Shipping label created
                        </Button>
                      </Form.Item>
                    </Col>
                  </Row>
                </Form>
              </div>
            </Modal>
            {/* Modal end */}
            <Modal
              visible={isShipmentDelivered}
              title="Update shipment delivered"
              onCancel={() => {
                this.setState({ isShipmentDelivered: false });
              }}
              destroyOnClose={true}
              width={800}
              footer={[
                <Button
                  type=""
                  onClick={() => {
                    this.setState({ isShipmentDelivered: false });
                  }}
                >
                  Cancel
                </Button>,
              ]}
            >
              <div className="form-design">
                <Form
                  layout="vertical"
                  onFinish={this.saveDelivery}
                  scrollToFirstError
                  ref={this.formRefDel}
                >
                  <Row>
                    <Col md={12}>
                      <Form.Item
                        name="delivery_date"
                        label="Delivery date"
                        rules={[
                          {
                            required: true,
                            message: "Please enter delivery date!",
                          },
                        ]}
                      >
                        <DatePicker
                          style={{ width: "100%" }}
                          format="DD-MM-YYYY"
                        />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="pod"
                        label="POD"
                        rules={[
                          {
                            required: true,
                            message: "Please select POD!",
                          },
                        ]}
                      >
                        <Upload
                          {...props}
                          data={{ shipping_id: shipping_id, doc_type: "POD" }}
                        >
                          <Button
                            icon={<UploadOutlined />}
                            style={{ width: "100%" }}
                          >
                            Click to Upload
                          </Button>
                        </Upload>
                      </Form.Item>
                    </Col>
                  </Row>
                  <Row>
                    <Col md={12}>
                      <Form.Item>
                        <Button type="primary" htmlType="submit" size="middle">
                          Save shipment delivered
                        </Button>
                      </Form.Item>
                    </Col>
                  </Row>
                </Form>
              </div>
            </Modal>
          </>
        }
      />
    );
  }
}

export default ViewShippingRequest;
