import React from "react";
import Headers from "../component/Headers";
import {
  Button,
  Divider,
  Tabs,
  Table,
  Switch,
  Spin,
  Row,
  Col,
  Input,
  Modal,
  notification,
} from "antd";
import { token } from "../../Helper/Session";
import { API, BASE_URL } from "../../Endpoint";
import { CheckOutlined, CloseOutlined } from "@ant-design/icons";
import { withRouter } from "react-router-dom";
import Form from "../../Helper/Form";

const { TabPane } = Tabs;

class EditCustomerUser extends React.Component {
  constructor(props) {
    const queryParams = new URLSearchParams(window.location.search);
    const id = queryParams.get("user_id");
    if (id == null) {
      props.history.push("/customer-users");
    }
    // console.log(props.form);
    super(props);
    this.state = {
      address: [],
      item: [],
      user: {},
      user_id: id,
      isLoadedAddress: true,
      isLoadSaveAddress: false,
      isLoadItem: true,
      isLoadSaveItem: false,
      idFormLoading: true,
      isItemModalVisible: false,
    };
  }
  formRef = React.createRef();
  async componentDidMount() {
    await fetch(`${BASE_URL}${API.USER_DETAIL}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({ user_id: this.state.user_id }),
    })
      .then((res) => res.json())
      .then((json) => {
        if (json.status === true) {
          notification.success({ message: json.success_msg });
          this.formRef.current.setFieldsValue({
            first_name: json.data.first_name,
            last_name: json.data.last_name,
            email: json.data.email,
            mobile: json.data.mobile,
          });
          this.setState({
            idFormLoading: false,
          });
        } else {
          notification({ message: json.error_msg });
        }
      });
  }

  loadAddress = (key) => {
    const user_id = this.state.user_id;
    if (key == 2 && this.state.isLoadedAddress) {
      this.setState({
        isLoadedAddress: false,
      });
      fetch(`${BASE_URL}${API.MAJOR_ADDRESS_LIST}`, {
        method: "POST",
        headers: {
          Accept: "application/form-data",
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({ user_id: user_id }),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json.status === true) {
            notification.success({ message: json.success_msg });
            this.setState({
              address: json.data,
            });
          } else {
            notification({ message: json.error_msg });
          }
        });
    } else if (key == 3 && this.state.isLoadItem) {
      this.setState({
        isLoadItem: false,
      });
      fetch(`${BASE_URL}${API.ITEM_LIST}`, {
        method: "POST",
        headers: {
          Accept: "application/form-data",
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({ user_id: user_id }),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json.status === true) {
            notification.success({ message: json.success_msg });
            this.setState({
              item: json.data,
            });
          } else {
            notification.error({ message: json.error_msg });
          }
        });
    }
  };
  onFinish = (values) => {
    this.setState({ idFormLoading: true });
    var user_data = [];
    user_data = {
      first_name: values.first_name,
      last_name: values.last_name,
      email: values.email,
      mobile: values.mobile,
      user_id: this.state.user_id,
    };
    console.log(user_data);
    fetch(`${BASE_URL}${API.SAVE_USER}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify(user_data),
    })
      .then((res) => res.json())
      .then((json) => {
        if (json.status === true) {
          notification.success({ message: json.success_msg });
          this.setState({ idFormLoading: false });
        } else {
          notification.error({ message: json.error_msg });
        }
      });
  };

  handleModal = () => {
    this.setState({ isModalVisible: true });
  };

  handleCancel = () => {
    this.setState({ isModalVisible: false });
  };

  handleOk = () => {
    this.setState({ isLoading: true });
  };

  handleSaveAddress = (values) => {
    this.setState({
      isLoadSaveAddress: true,
    });
    values.user_id = this.state.user_id;
    fetch(`${BASE_URL}.${API.ADD_MAJOR_ADDRESS}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify(values),
    })
      .then((res) => res.json())
      .then((json) => {
        this.setState({
          isLoadSaveAddress: false,
        });
        if (json.status === true) {
          this.setState({
            isLoadedAddress: true,
            isModalVisible: false,
          });
          this.loadAddress(2);
          notification.success({ message: json.success_msg });
        } else {
          notification.error({ message: json.error_msg });
        }
      });
  };

  handleSaveItem = (values) => {
    this.setState({ isLoadSaveItem: true });
    values.user_id = this.state.user_id;
    fetch(`${BASE_URL}.${API.ADD_ITEMS}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify(values),
    })
      .then((res) => res.json())
      .then((json) => {
        this.setState({ isLoadSaveItem: false });
        if (json.status === true) {
          this.setState({
            isLoadItem: true,
            isItemModalVisible: false,
          });
          this.loadAddress(3);
          notification.success({ message: json.success_msg });
        } else {
          notification.error({ message: json.error_msg });
        }
      });
  };
  render() {
    const {
      address,
      item,
      user,
      idFormLoading,
      isModalVisible,
      isItemModalVisible,
      isLoadSaveAddress,
      isLoadSaveItem
    } = this.state;
    var data = [];
    var data_item = [];
    const columns = [
      {
        title: "SNo",
        dataIndex: "key",
      },
      {
        title: "Company Name",
        dataIndex: "company_name",
      },
      {
        title: "Contact Name",
        dataIndex: "contact_name",
      },
      {
        title: "Contact Number",
        dataIndex: "mobile",
      },
      {
        title: "Email",
        dataIndex: "email",
      },
      {
        title: "Street",
        dataIndex: "street",
      },
      {
        title: "City",
        dataIndex: "city",
      },
      {
        title: "State",
        dataIndex: "state",
      },
      {
        title: "Country",
        dataIndex: "country",
      },
    ];
    const columns_item = [
      {
        title: "Sno",
        dataIndex: "key",
      },
      {
        title: "Item name",
        dataIndex: "name",
      },
      {
        title: "Description",
        dataIndex: "description",
      },
    ];

    address.map((addr, i) =>
      data.push({
        key: i + 1,
        company_name: addr["company_name"],
        contact_name: addr["contact_name"],
        mobile: addr["mobile"],
        email: addr["email"],
        street: addr["street"],
        city: addr["city"],
        state: addr["state"],
        country: addr["country"],
        status: (
          <Switch
            size="default"
            checkedChildren={<CheckOutlined />}
            unCheckedChildren={<CloseOutlined />}
            defaultChecked
          />
        ),
        action: (
          <Button
            type="primary"
            size="medium"
            style={{ background: "#ffc107", border: "none" }}
          >
            Edit
          </Button>
        ),
      })
    );

    item.map((itm, i) =>
      data_item.push({
        key: i + 1,
        name: itm["name"],
        description: itm["description"],
      })
    );
    return (
      <Headers
        bodyContain={
          <>
            <div className="site-layout-background form-design">
              <h4>Edit Customer User Detail</h4>
              <Divider />
              <Tabs tabBarExtraContent={""} onTabClick={this.loadAddress}>
                <TabPane tab="User Detail" key="1">
                  {/* user detail */}
                  <Spin spinning={idFormLoading}>
                    <Form
                      ref={this.formRef}
                      name="register"
                      layout="vertical"
                      onFinish={this.onFinish}
                      scrollToFirstError
                    >
                      <Row>
                        <Col md={12}>
                          <Form.Item
                            name="first_name"
                            label="First Name"
                            value={user.first_name}
                            rules={[
                              {
                                required: true,
                                message: "Please input user first name!",
                                whitespace: true,
                              },
                            ]}
                          >
                            <Input value={user.first_name} />
                          </Form.Item>
                        </Col>
                        <Col md={12}>
                          <Form.Item
                            name="last_name"
                            label="Last Name"
                            onChange={this.handleChange}
                            initialValue={user.last_name}
                            value={user.last_name}
                            rules={[
                              {
                                required: true,
                                message: "Please input user last name!",
                                whitespace: true,
                              },
                            ]}
                          >
                            <Input
                            // initialvalues={user.last_name}
                            // onChange={this.changeBody}
                            />
                          </Form.Item>
                        </Col>
                        <Col md={12}>
                          <Form.Item
                            name="email"
                            label="E-mail"
                            rules={[
                              {
                                type: "email",
                                message: "The input is not valid E-mail!",
                              },
                              {
                                required: true,
                                message: "Please input your E-mail!",
                              },
                            ]}
                          >
                            <Input />
                          </Form.Item>
                        </Col>
                        <Col md={12}>
                          <Form.Item
                            name="mobile"
                            label="Contact Number"
                            rules={[
                              {
                                required: true,
                                message: "Please input your contact number!",
                              },
                            ]}
                          >
                            <Input />
                          </Form.Item>
                        </Col>
                      </Row>
                      <Row>
                        <Col md={12}>
                          <Form.Item>
                            <Button
                              type="primary"
                              htmlType="submit"
                              size="middle"
                            >
                              Save
                            </Button>
                          </Form.Item>
                        </Col>
                      </Row>
                    </Form>
                  </Spin>
                </TabPane>
                <TabPane tab="Major Address" key="2">
                  {/* address list */}
                  <Row>
                    <Col offset={21}>
                      <Button
                        type="secondary"
                        style={{ marginBottom: "10px" }}
                        onClick={this.handleModal}
                      >
                        Add Address
                      </Button>
                    </Col>
                    <Col md={24}>
                      <Table
                        columns={columns}
                        dataSource={data}
                        size="middle"
                        bordered
                        loading="false"
                      />
                    </Col>
                  </Row>
                </TabPane>
                <TabPane tab="Items" key="3">
                  {/* items list */}
                  <Row>
                    <Col offset={21}>
                      <Button
                        type="secondary"
                        style={{ marginBottom: "10px" }}
                        onClick={() =>
                          this.setState({ isItemModalVisible: true })
                        }
                      >
                        Add item
                      </Button>
                    </Col>
                    <Col md={24}>
                      <Table
                        columns={columns_item}
                        dataSource={data_item}
                        size="middle"
                        bordered
                        loading="false"
                      />
                    </Col>
                  </Row>
                </TabPane>
              </Tabs>
            </div>
            <Modal
              title="Add major addresses"
              style={{ top: 20 }}
              visible={isModalVisible}
              width={790}
              onCancel={() => {
                this.setState({ isModalVisible: false });
              }}
              destroyOnClose={true}
              footer={[
                <Button
                  key="cancel"
                  type="default"
                  loading=""
                  onClick={this.handleCancel}
                >
                  Cancel
                </Button>,
              ]}
            >
              <div className="form-design">
                <Form
                  ref={this.addrForm}
                  name="register"
                  layout="vertical"
                  onFinish={this.handleSaveAddress}
                  scrollToFirstError
                >
                  <Row>
                    <Col md={12}>
                      <Form.Item
                        name="company_name"
                        label="Company Name"
                        rules={[
                          {
                            required: true,
                            message: "Please enter company name!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="contact_name"
                        label="Contact Name"
                        rules={[
                          {
                            required: true,
                            message: "Please enter contact name!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="email"
                        label="Email"
                        rules={[
                          {
                            required: true,
                            message: "Please enter email!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="mobile"
                        label="Contact number"
                        rules={[
                          {
                            required: true,
                            message: "Please enter contact mobile number!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="street"
                        label="Street"
                        rules={[
                          {
                            required: true,
                            message: "Please enter street address!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>

                    <Col md={12}>
                      <Form.Item
                        name="city"
                        label="City"
                        rules={[
                          {
                            required: true,
                            message: "Please enter city",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>

                    <Col md={12}>
                      <Form.Item
                        name="postal"
                        label="Postal"
                        rules={[
                          {
                            required: true,
                            message: "Please enter postal",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>

                    <Col md={12}>
                      <Form.Item
                        name="state"
                        label="State"
                        rules={[
                          {
                            required: true,
                            message: "Please enter state",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>

                    <Col md={12}>
                      <Form.Item
                        name="country"
                        label="Country"
                        rules={[
                          {
                            required: true,
                            message: "Please enter Country",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                  </Row>
                  <Row>
                    <Col md={12}>
                      <Form.Item>
                        <Button
                          type="primary"
                          htmlType="submit"
                          size="middle"
                          loading={isLoadSaveAddress}
                        >
                          Save Address
                        </Button>
                      </Form.Item>
                    </Col>
                  </Row>
                </Form>
              </div>
            </Modal>

            <Modal
              title="Add Items"
              style={{ top: 20 }}
              visible={isItemModalVisible}
              width={790}
              destroyOnClose={true}
              onCancel={() => {
                this.setState({
                  isItemModalVisible: false,
                });
              }}
              footer={[
                <Button
                  key="cancel"
                  type="default"
                  loading=""
                  onClick={() => this.setState({ isItemModalVisible: false })}
                >
                  Cancel
                </Button>,
              ]}
            >
              <div className="form-design">
                <Form
                  name="add_item"
                  layout="vertical"
                  onFinish={this.handleSaveItem}
                  scrollToFirstError
                >
                  <Row>
                    <Col md={12}>
                      <Form.Item
                        name="name"
                        label="Item Name"
                        rules={[
                          {
                            required: true,
                            message: "Please enter item name!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="description"
                        label="Item description"
                        rules={[
                          {
                            required: true,
                            message: "Please enter item description!",
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                  </Row>
                  <Row>
                    <Col md={12}>
                      <Form.Item>
                        <Button
                          type="primary"
                          htmlType="submit"
                          size="middle"
                          loading={isLoadSaveItem}
                        >
                          Save Items
                        </Button>
                      </Form.Item>
                    </Col>
                  </Row>
                </Form>
              </div>
            </Modal>
          </>
        }
      />
    );
  }
}

export default withRouter(EditCustomerUser);
