import React from "react";
import Headers from "../component/Headers";
import { BASE_URL, API } from "../../Endpoint";
import { Row, Col, Button, Input, Divider, notification, Spin } from "antd";
import Form from "../../Helper/Form";
import { token } from "../../Helper/Session";

class EditCustomerAccount extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user: [],
      id: props.id,
      isLoading: true,
    };
  }

  formRef = React.createRef();
  async componentDidMount() {
    // console.log(token);
    await  fetch(`${BASE_URL}${API.CUSTOMER_ACCOUNT_DETAIL}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({ user_id: this.state.id }),
    })
      .then((res) => res.json())
      .then((json) => {
        if (json.status === true) {
          this.setState({
            user: json.data,
          });
          this.formRef.current.setFieldsValue({
            customer_account_name: this.state.user.customer_account_name,
            nickname: this.state.user.nickname,
          });
        }
        this.setState({
          isLoading: false,
        });
      });
  }

  render() {
    const { id, isLoading } = this.state;
    const onFinish = (values) => {
      const sub_data = {
        customer_account_name: values.customer_account_name,
        nickname: values.nickname,
        user_id: id,
      };
      this.setState({ isLoading: true });
      fetch(`${BASE_URL}${API.UPDATE_CUSTOMER_ACCOUNT}`, {
        method: "POST",
        headers: {
          Accept: "application/form-data",
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(sub_data),
      })
        .then((res) => res.json())
        .then((json) => {
          this.setState({ isLoading: false });
          if (json.status === true) {
            notification.success({ message: json.success_msg });
            this.props.history.push("/customer-account");
          } else {
            notification.error({ message: json.error_msg });
          }
        });
    };
    return (
      <Headers
        bodyContain={
          <>
            <div className="site-layout-background form-design">
              <h4>Add Customer Account</h4>
              <Divider />
              <Form
                name="register"
                layout="vertical"
                onFinish={onFinish}
                scrollToFirstError
                ref={this.formRef}
              >
                <Spin size="middle" spinning={isLoading}>
                  <Row>
                    <Col md={12}>
                      <Form.Item
                        name="customer_account_name"
                        label="Customer Account Name"
                        rules={[
                          {
                            required: true,
                            message: "Please input customer account name!",
                            whitespace: true,
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col md={12}>
                      <Form.Item
                        name="nickname"
                        label="Customer Account Short Name"
                        rules={[
                          {
                            required: true,
                            message:
                              "Please input Customer Account Short Name!",
                            whitespace: true,
                          },
                        ]}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                  </Row>
                </Spin>

                <Row>
                  <Col md={12}>
                    <Form.Item>
                      <Button type="primary" htmlType="submit" size="large">
                        Register
                      </Button>
                    </Form.Item>
                  </Col>
                </Row>
              </Form>
            </div>
          </>
        }
      />
    );
  }
}
export default EditCustomerAccount;
