import React from "react";
import Headers from "./component/Headers";
import { Card, Button, Input, message } from "antd";
import { UnorderedListOutlined, FolderAddOutlined } from "@ant-design/icons";
import { Col, Row } from "antd";
import { API, BASE_URL } from "../Endpoint";
import { token } from "../Helper/Session";
import { withRouter } from "react-router-dom";

const { Search } = Input;
class Home extends React.Component {
  state = {
    loading: false,
    stats: [],
    placed: 0,
    create: 0,
    transit: 0,
    delivery: 0,
    track_data: [],
    isSearching: false
  };

  handleTracking = (value) => {
    this.setState({ isSearching: true });
    fetch(`${BASE_URL}${API.SHIPMENT_TRACKING}`, {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        tracking_id: value,
      }),
    })
      .then((res) => res.json())
      .then((json) => {
        this.setState({ isSearching: false });
        if (json.status == true) {
          this.setState({
            track_data: json.data,
          });
          this.props.history.push("/shipping-request/view/" + json.data.id);
        } else {
          message.error({
            content: json.error_msg,
            duration: 2,
          });
        }
      });
  };

  componentDidMount() {
    this.setState({ loading: true });
    fetch(`${BASE_URL}${API.DASHBOARD}`, {
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
    })
      .then((res) => res.json())
      .then((json) => {
        this.setState({ loading: false });
        if (json.status === true) {
          this.setState({ stats: json.data });
        }
      });
  }
  render() {
    const { loading, stats, isSearching } = this.state;
    stats.map((st, i) => {
      if (st.status == 0) {
        this.state.placed = st.count;
      } else if (st.status == 1) {
        this.state.create = st.count;
      } else if (st.status == 2) {
        this.state.transit = st.count;
      } else if (st.status == 3) {
        this.state.delivery = st.count;
      }
    });
    return (
      <Headers
        bodyContain={
          <>
            <Row>
              <Col md={6} sm={24} xs={24}>
                <Card
                  className="card border-left-primary"
                  onClick={
                    () =>
                      this.props.history.push(
                        "/shipping-request-list/?status=4"
                      ) /* for parameter is to check it is in request placed*/
                  }
                  loading={loading}
                >
                  <Row>
                    <Col xs={20}>
                      <h5 className="font-weight-bold text-primary">
                        Request Placed
                      </h5>
                      <p>{this.state.placed}</p>
                    </Col>
                    <Col xs={4}>
                      <UnorderedListOutlined className="icon_size" />
                    </Col>
                  </Row>
                </Card>
              </Col>
              <Col md={6} sm={24} xs={24}>
                <Card
                  className="card border-left-success"
                  loading={loading}
                  onClick={() =>
                    this.props.history.push("/shipping-request-list/?status=1")
                  }
                >
                  <Row>
                    <Col xs={20}>
                      <h5 className="font-weight-bold text-success">
                        Label Created
                      </h5>
                      <p>{this.state.create}</p>
                    </Col>
                    <Col xs={4}>
                      <UnorderedListOutlined className="icon_size" />
                    </Col>
                  </Row>
                </Card>
              </Col>
              <Col md={6} sm={24} xs={24}>
                <Card
                  className="card border-left-warning"
                  loading={loading}
                  onClick={() =>
                    this.props.history.push("/shipping-request-list/?status=2")
                  }
                >
                  <Row>
                    <Col xs={20}>
                      <h5 className="font-weight-bold text-warning">
                        Intransit
                      </h5>
                      <p>{this.state.transit}</p>
                    </Col>
                    <Col xs={4}>
                      <UnorderedListOutlined className="icon_size" />
                    </Col>
                  </Row>
                </Card>
              </Col>
              <Col md={6} sm={24} xs={24}>
                <Card
                  className="card border-left-info"
                  loading={loading}
                  onClick={() =>
                    this.props.history.push("/shipping-request-list/?status=3")
                  }
                >
                  <Row>
                    <Col xs={20}>
                      <h5 className="font-weight-bold text-info">Delivered</h5>
                      <p>{this.state.delivery}</p>
                    </Col>
                    <Col xs={4}>
                      <UnorderedListOutlined className="icon_size" />
                    </Col>
                  </Row>
                </Card>
              </Col>
            </Row>
            <Row>
              <Col md={6} sm={24} xs={24}>
                <Button
                  type="secondary"
                  onClick={() =>
                    this.props.history.push("/add-shipping-request")
                  }
                  className="add_shipping_btn"
                >
                  Add Shipping Request <br />
                  <FolderAddOutlined style={{ fontSize: "40px" }} />
                </Button>
              </Col>
              <Col md={12} sm={24} xs={24}>
                <Card className="card">
                  <Row>
                    <Col xs={24}>
                      <span>Please enter tracking id to search the shipment.</span>
                      <Search
                        type="secondary"
                        placeholder="Enter shipment tracking Id"
                        enterButton="Search"
                        size="medium"
                        loading={isSearching}
                        onSearch={this.handleTracking}
                      />
                    </Col>
                  </Row>
                </Card>
              </Col>
            </Row>
          </>
        }
      />
    );
  }
}

export default withRouter(Home);
