import React from "react";
import Headers from "../component/Headers";
import { BASE_URL, API } from "../../Endpoint";
import { Row, Col, Table, Button, Switch, notification } from "antd";
import { withRouter } from "react-router-dom";
import { token } from "../../Helper/Session";

class CustomerAccount extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
    };
  }

  goToAddUser = () => {
    this.props.history.push("/add-customer-account");
  };

  componentDidMount() {
    fetch(`${BASE_URL}${API.CUSTOMER_ACCOUNT_LIST}`, {
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
    })
      .then((res) => res.json())
      .then((json) => {
        if (json.status === true) {
          this.setState({
            users: json.data,
          });
          notification.success({ message: json.success_msg });
        } else {
          notification.error({ message: json.error_msg });
        }
      });
  }

  render() {
    const { users } = this.state;
    // console.log(user);
    var data = [];
    const columns = [
      {
        title: "SNo",
        dataIndex: "key",
      },
      {
        title: "Customer Account Name",
        dataIndex: "name",
      },
      {
        title: "Short Name",
        dataIndex: "nickname",
      },
      {
        title: "Status",
        dataIndex: "status",
      },
      {
        title: "Action",
        dataIndex: "action",
      },
    ];
    users.map((usr, i) =>
      data.push({
        key: i + 1,
        name: usr["customer_account_name"],
        nickname: usr["nickname"],
        status: (
          <Switch
            size="default"
            checkedChildren= "Active"
            unCheckedChildren="InActive"
            defaultChecked={usr['status'] == 1 ? true : false}
            style={{
              width:"80px"
            }}
          />
        ),
        action: (
          <Button
            type="primary"
            size="medium"
            style={{ background: "#ffc107", border: "none" }}
            onClick={() => this.props.history.push('edit-customer-account/'+usr['id'])}
          >
            Edit
          </Button>
        ),
      })
    );

    return (
      <Headers
        bodyContain={
          <>
            <Row>
              <Col offset={20}>
                <Button
                  onClick={this.goToAddUser}
                  type="primary"
                  style={{ marginBottom: "10px" }}
                  shape="round"
                >
                  Add Customer Account
                </Button>
              </Col>
            </Row>
            <div className="site-layout-background">
              <Row>
                <Col md={24}>
                  <Table
                    columns={columns}
                    dataSource={data}
                    size="middle"
                    bordered
                    loading="false"
                  />
                </Col>
              </Row>
            </div>
          </>
        }
      />
    );
  }
}
export default withRouter(CustomerAccount);
